html{box-sizing:border-box;font-family:"Roboto",sans-serif}*,*:before,*:after{font-family:inherit;box-sizing:inherit;margin:0;padding:0}html,body{height:100%}:root{--bg: hsl(0, 0%, 98%);--bg-offset: hsl(0, 0%, 100%);--text: hsl(200, 15%, 8%);--gray: hsl(0, 0%, 52%);--txtbox: hsl(0, 0%, 33%);--border: rgba(0, 0, 0, 0.1)}[data-theme=light]{--bg: hsl(0, 0%, 98%);--bg-offset: hsl(0, 0%, 100%);--text: hsl(200, 15%, 8%);--gray: hsl(0, 0%, 52%);--border: rgba(0, 0, 0, 0.1)}[data-theme=dark]{--bg: hsl(207, 26%, 17%);--bg-offset: hsl(209, 23%, 22%);--text:	hsl(0, 0%, 83%);--border: rgba(193, 183, 176, 1);--gray: hsl(0, 0%, 90%)}body,h1,h2,h3,h4,h5,h6,p,blockquote,pre,hr,dl,dd,ol,ul,figure{margin:0;padding:0}input,textarea,button{font-family:inherit}textarea{resize:none}body{font-size:14px;background-color:var(--bg);color:var(--text)}h1,h2,h3,h4,h5,h6{font-weight:600;font-style:normal;font-display:swap}h1{font-size:22px}p{line-height:1.4;max-width:50em;margin-bottom:1.5em;font-size:var(--font-size)}p.margin-btm-reset{margin-bottom:0px}a{text-decoration:none;color:var(--color-primary)}button{background:rgba(0,0,0,0);cursor:pointer;border:0}li>ul,li>ol{margin-bottom:0}dt{font-weight:700}img{max-width:100%;font-style:italic;vertical-align:middle}img[width],img[height]{max-width:none}svg{margin-bottom:3px}q{quotes:"‘" "’"}q:before{content:open-quote}q:after{content:close-quote}blockquote{quotes:"“" "”"}ol,ul{list-style:none}svg{fill:currentColor;vertical-align:middle}.wrapper{width:94%;max-width:1440px;margin-left:auto;margin-right:auto}.margin-btm{margin-bottom:4em}.container{display:flex}.flex{display:flex}.flex-wrap{flex-wrap:wrap}.f-center-x{justify-content:center}.f-center-y{align-items:center}.f-btw{justify-content:space-between}